import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import { toast } from 'react-toastify';

import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import SearchIcon from '@material-ui/icons/Search';
import { push } from 'react-router-redux';
import './Header.css';
import { store } from '../../../store';
import {
  CLEAR_CART,
} from '../../../constants/actionTypes';
import Logo from '../../../common/Components/Logo';
import { DEFAULT_BG_STYLE, DEFAULT_TEXT_COLOR } from '../../../constants/otherConstants';
import lightOrDark from '../../../common/Components/checkBgColor';

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: { profile_pic: null, link: [], username: '' },
      mobileView: false,
      render: false,
      shippingAddress: '',
    };

    this.cartItems = [];
  }

  componentWillMount() {
    window.addEventListener('resize', this.resize.bind(this));
    this.resize();
    const {
      parent, user, cartItems, clearCart, homeBgStyle, publicPageBgStyle,
    } = this.props;
    const { render } = this.state;
    if (parent) {
      if (parent === 'admin-page') {
        if (user) {
          this.setState({ user: user.user });
        }
        if (homeBgStyle) {
          let data = JSON.parse(DEFAULT_BG_STYLE);
          let textColor = DEFAULT_TEXT_COLOR;
          if (homeBgStyle.background_color) {
            try {
              data = JSON.parse(homeBgStyle.background_color);
              textColor = lightOrDark(data.backgroundColor);
            } catch {
              data = JSON.parse(DEFAULT_BG_STYLE);
            }
          }
          if (homeBgStyle.link_style) {
            textColor = homeBgStyle.link_style;
          }
          if (homeBgStyle.background_image) {
            data = { ...data, backgroundImage: `${homeBgStyle.background_image}` };
          }
          this.setState({ bgStyle: data, textColor });
        }
      }
    } else if (publicPageBgStyle) {
      let data = {};
      let textColor = DEFAULT_TEXT_COLOR;
      if (publicPageBgStyle.background_color) {
        try {
          data = JSON.parse(publicPageBgStyle.background_color);
          textColor = lightOrDark(data.backgroundColor);
        } catch {
          data = JSON.parse(DEFAULT_BG_STYLE);
        }
      }
      if (publicPageBgStyle.link_style) {
        textColor = publicPageBgStyle.link_style;
      }

      if (publicPageBgStyle.background_image) {
        data = { ...data, backgroundImage: `${publicPageBgStyle.background_image}` };
      }
      this.setState({ bgStyle: data, textColor });
    }

    if (cartItems) {
      this.cartItems = cartItems;
      if (this.state.user.id) {
        if (this.cartItems.filter((i) => i.item.user !== this.state.user.id).length > 0) {
          clearCart();
        }
      }
      this.setState({ render: !render });
    }
  }

  componentWillReceiveProps(np) {
    const {
      shippingAddress, user, render,
    } = this.state;
    const { clearCart, parent, setPublicPageUserDetails } = this.props;
    if (parent === 'admin-page') {
      if (np.user && np.user.user !== user) {
        this.setState({ user: np.user.user });
        setPublicPageUserDetails({ user: np.user.user });
      }
      if (np.homeBgStyle) {
        let data = {};
        let textColor = DEFAULT_TEXT_COLOR;
        if (np.homeBgStyle.background_color) {
          try {
            data = JSON.parse(np.homeBgStyle.background_color);
            textColor = lightOrDark(data.backgroundColor);
          } catch {
            data = JSON.parse(DEFAULT_BG_STYLE);
          }
        }

        if (np.homeBgStyle.link_style) {
          textColor = np.homeBgStyle.link_style;
        }

        if (np.homeBgStyle.background_image) {
          data = { ...data, backgroundImage: `${np.homeBgStyle.background_image}` };
        }
        this.setState({ bgStyle: data, textColor });
      }
    } else {
      if (np.publicPageUser) {
        this.links = np.publicPageUser.links;
        this.setState({ user: np.publicPageUser });
      }
      if (np.publicPageBgStyle) {
        let data = {};
        let textColor = DEFAULT_TEXT_COLOR;
        if (np.publicPageBgStyle.background_color) {
          try {
            data = JSON.parse(np.publicPageBgStyle.background_color);
            textColor = lightOrDark(data.backgroundColor);
          } catch {
            data = JSON.parse(DEFAULT_BG_STYLE);
          }
        }
        if (np.publicPageBgStyle.link_style) {
          textColor = np.publicPageBgStyle.link_style;
        }
        if (np.publicPageBgStyle.background_image) {
          data = { ...data, backgroundImage: `${np.publicPageBgStyle.background_image}` };
        }
        this.setState({ bgStyle: data, textColor });
      }
      if (np.publicPageShopDetails) {
        if (np.publicPageShopDetails.address !== shippingAddress) {
          this.setState({ shippingAddress: np.publicPageShopDetails.address });
          toast.info(np.publicPageShopDetails.address);
        }
      }
      if (np.cartItems && np.cartItems !== this.cartItems) {
        this.cartItems = np.cartItems;
        this.setState({ render: !render });
      }
      if (user.id && np.cartItems) {
        if (this.cartItems.filter((i) => i.item.user !== user.id).length > 0) {
          clearCart();
        }
      }
    }
  }

  resize() {
    this.setState({ mobileView: window.innerWidth <= 600 });
  }

  showCart() {
    const path = window.location.search ? `${window.location.pathname}${window.location.search}&cartDetails=true`
      : `${window.location.pathname}?cartDetails=true`;
    store.dispatch(push(path));
  }

  render() {
    const {
      user, shippingAddress, mobileView, textColor, bgStyle,
    } = this.state;
    const { username } = user;
    if (mobileView) {
      return (
        < >
          <nav className="home-page-nav-div" style={{background: textColor}} >
            <div className="search-div">
              <input placeholder="Search " />
              <SearchIcon />
            </div>
            <button className="cart-div " type="button" onClick={this.showCart}>
              <ShoppingCartIcon />

              <span>
                Cart(
                {this.cartItems.length}
                )
              </span>
            </button>

          </nav>
          <header className="home-page-top-header" style={{...bgStyle, borderBottom : `1px solid ${textColor}`}}>
            <div className="left-div">
              <Logo
                logostyles={{
                  display: 'flex', flexDirection: 'row', color: textColor,
                }}
                parent={!this.props.parent ? 'public-home-page' : this.props.parent}
                username={username}
              />
            </div>
            <div className="right-div">
              <div className="shipping-area" style={{color:textColor}} >
                {`${shippingAddress}`}
              </div>
            </div>
          </header>
        </>
      );
    }
    return (
      < >
        <header className="home-page-top-header"  style={bgStyle}>
          <div className="left-div">
            <Logo
              logostyles={{
                display: 'flex', flexDirection: 'row', color: textColor,
              }}
              parent={!this.props.parent ? 'public-home-page' : this.props.parent}
              username={username}
            />
          </div>
          <div className="right-div">
            <div className="shipping-area" style={{color:textColor}}>
              
              {`${shippingAddress}`}
              
            </div>
            <div className="bottom-div">
              <div className="search-div">
                <input />
                <SearchIcon />
              </div>
              <button className="cart-div " type="button" onClick={this.showCart}>
                <ShoppingCartIcon />

                <span>
                  Cart(
                  {this.cartItems.length}
                  )
                </span>
              </button>
            </div>

          </div>
        </header>
        <nav className="home-page-nav-div" style={{background: textColor}}  />
      </>
    );
  }
}


const mapStateToProps = (state) => ({
  cartItems: state.HomePageReducers.cartItems,
  publicPageShopDetails: state.HomePageReducers.publicPageShopDetails,
  publicPageBgStyle: state.HomePageReducers.publicPageBgStyle,
  homeBgStyle: state.AdminPageReducers.homeBgStyle,
});

const mapDispatchToProps = (dispatch) => ({
  clearCart: () => store.dispatch({ type: CLEAR_CART }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Header);
